/* ===== Vars globais ===== */
:root {
  --gap: clamp(24px, 3vw, 56px);
  --copy-max: 560px;
  --grad-h: 96px;
  --grad: linear-gradient(90deg, #0066B3 0%, #1B9B54 100%);
  --title-color: #16181d;
}

* {
  box-sizing: border-box;
}

/* ===== Seo ===== */
.ric-where {
  position: relative;
  min-height: auto;
  display: grid;
  place-items: center;
  padding-top: 3rem;
  overflow: visible;
  background: #fff;
}

@media only screen and (min-width: 993px) {
  .ric-where {
    min-height: 60vh;
  }
}

/* container central (texto + mapa) */
.ric-grid {
  width: 100%;
  max-width: 1200px;
  margin-inline: auto;
  display: grid;
  grid-template-columns: minmax(320px, var(--copy-max)) 1fr;
  align-items: center;
  gap: var(--gap);
  position: relative;
}

/* SEO DE TEXTO (logo + copy) */
.ric-text-section {
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: var(--copy-max);
  position: relative;
  z-index: 1;
}

/* LOGO - fica fora do ric-copy */
.ric-logo {
  width: 150px;
  height: auto;
  display: block;
  padding-left: 2rem;
}

/* BLOCO DE TEXTO */
.ric-copy {
  position: relative;
  /* ancora o ::before */
  z-index: 1;
  /* texto sobre o gradiente */
  padding-left: 2rem;
  color: #2A2A2A;
}

/* GRADIENTE infinito da esquerda da tela at o texto */
.ric-copy::before {
  content: "";
  position: absolute;
  right: 100%;
  /* encosta a borda direita no incio do texto */
  width: 100vw;
  /* estica at o limite da tela */
  height: var(--grad-h);
  top: 50%;
  transform: translateY(-50%);
  background: var(--grad);
  z-index: -1;
  /* vai atrs do contedo */
}

/* MAPA - bem maior */
.ric-map {
  display: grid;
  place-items: center;
}

.ric-map img {
  width: 600px;
  height: auto;
  display: block;
  filter: drop-shadow(0 12px 32px rgba(13, 32, 62, 0.2));
}

/* ===== Responsivo ===== */
@media (min-width: 1200px) {
  .ric-map img {
    width: 800px;
    max-width: 800px;
  }
}

@media (min-width: 1380px) {
  .ric-map img {
    width: 1000px;
    max-width: 1000px;
  }
}

@media (max-width: 991.98px) {
  .ric-grid {
    grid-template-columns: 1fr;
    gap: clamp(16px, 4vw, 28px);
    padding-inline: clamp(16px, 5vw, 28px);
  }
  .ric-text-section {
    order: 1;
  }
  .ric-copy::before {
    height: 72px;
    /* opcional */
  }
  .ric-map {
    order: 2;
  }
  .ric-map img {
    width: 100%;
    max-width: none;
  }
}

@media (prefers-color-scheme: dark) {
  .ric-where {
    background: #0b1520;
  }
  .ric-title {
    color: #eef2f6;
  }
}

